.Header{
    background-color:  var(--color-primary);
    width: 100vw;
    min-height: 3em;
    display: grid;
    grid-template-columns: auto 1fr 1fr;
    grid-template-rows:1fr ;
    align-items: center;
    padding: 10px;

}


.userContainer{
display: grid;
grid-template-columns:auto 1fr;
grid-template-rows:1fr;
grid-auto-flow: 10px;
justify-content: center;
align-items: center;
text-align: center;
}
.boxCod{
    margin-left: 2vw;
    font-weight: bold;
}
.imgUser{
    background-color: #ddd;
    height: 3em;
    width: 3em;
    border-radius: 100%;
    cursor: pointer;
}

.imgUser img{
    height: 100%;
    width: 100%;

}
.cod{
    display: flex;
    justify-content: center;
    align-items: center;
}
.cod img{
 display: flex;
 justify-content: center;
 align-items: center;
 border-radius: 0px;
 margin-left: .5vw;
 cursor: pointer;
}