/* app tododinho */

body,html{
  overflow-x: hidden;
}
*{
  box-sizing: content;
  padding: 0;
  margin: 0;
}
:root{
  --color-primary: lightsalmon;
  --color-secondary:lavender;
  --color-bt-pri:#A2C154;
  --color-bt-sec:#B9CA82;
  --color-font:#282c34;
  --border-input:#ECE9EC;
}

.App {
  background-color: #282c34;
  min-height: 100vh;
  font-size: calc(10px + 2vmin);
  color: white;
}

*:focus{
  outline: none;
}
.Login{
  width: 100vw;
  height: 100vh;
  background-color: lavender;
  display: grid;
  grid-template-columns: 1fr .5fr;
  color:rgb(126, 126, 126)
}
.backgroundLogin{
  background-color: var(--color-primary);
  min-height: 100vh;
  min-width: 20vw;
  max-width: 80vw;
}

/* loginnnn */
.areaLogin{
  background-color: white;
  height: 100%;
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 8%;
}

.title{
  margin: 5px+1vh;
  font-size: 1em;
  margin-bottom: 5vh;
}
.firebaseAuternativo{
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
}
.googleLogin{
  background-color: #ff6347;
  width: 90%;
  padding: 5px;
  border-radius: 5px;
  color: white;
  font-weight: bold;
  font-size: 0.9em;
  cursor: pointer;
  display: flex;
  justify-content: center;
  align-items: center;
  
}

img{
height: 5vh;
border-radius: 100%;
margin-bottom: 0vh;
margin-right: 2vh;
display: flex;
justify-content: center;
align-items: center;
}

form {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
}

button{
  width: fit-content;
  padding: 10px+3vh;
  background-color: var(--color-bt-pri);
  border-color: var(--color-bt-sec);
  border-style: solid;
  border-radius: 5px;
  color: white;
  font-weight: bold;
  letter-spacing: .1em;
  font-size: .9em;
  cursor: pointer;
}
input{
  width: 90%;
  margin-bottom: 2vh;
  padding: 3%;
  border-color: var(--border-input);
  border-style: solid;
  border-radius: 5px;
  
}
input:focus{
  
  border-color:  var(--color-primary);
}

.redefinir{
color: var(--color-primary);
margin-bottom: 2vh;
}

.registre{
  text-decoration: none;
  font-weight: bold;
  color: rgba(39, 39, 39, 0.867);
  font-size: 0.9em;
  margin:5%;
}

/* redefinir */

.Redefinir{
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100vw;
  height: 100vh;
  background-color:  var(--color-primary);
}

.Redefinir .inPredefinir:focus{
  box-shadow: 0px 0px 5vh rgba(0,0,0,.2);
}
input{
  font-size: 0.9em;
  color: #282c34;
}

.Redefinir .title{
  font-size: 1.2em;
  
}
.Redefinir .lembrei{
  text-decoration: none;
  margin-top: 3vh;
  color: #fff;
  font-weight: bold;
}

/* painel */

.Painel{
  background-color: rgb(255, 219, 205);
  min-height: 100vh;
  color: #282c34;
}




/* alerta */
.alerta{
  box-shadow:inset 0px -.5em rgba(129, 136, 110, 0.623);
  position: fixed;
  z-index: 1000;
  top: 0;
  left: 0;
  right: 0;
  margin: auto;
  width: 50vw;
  min-height: 4em;
  border-bottom-left-radius:10px;
  border-bottom-right-radius:10px;
  background-color: rgb(75, 199, 102);
  padding: 3vh;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 1em;
  text-transform: uppercase;
  color: aliceblue;
  font-weight: bold;

}



/* responsividade */

@media only screen and (max-device-width: 700px) {
  .Login{
    display: flex;
    justify-content: center;
    align-items: center;
    box-sizing: content;
  }
  .backgroundLogin{
    visibility: hidden;
    display: none;
  }
  .areaLogin{
    min-height: 100%;
    min-width: 100vw;
    padding: 8%;
  }
  .alerta{
    width: 80vw;
    font-size: 1em;
  
  }

}